  .btn {
    
  display: inline-block;
  padding: 10px 20px;
  margin: 10px;
  font-size: 16px;
  color: #fff;
  background-color: #8A2BE2;
  border: none;
  border-radius: 5px;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s ease, transform 0.2s ease;
  
  }

  .btn:hover {
    background-color: #6c1db5;
    transform: scale(1.05);
  }
  
  .btn:active {
    background-color: #58169a;
    transform: scale(0.98);
  }

  .btn2 {
    display: inline-block;
    padding: 10px 20px;
    margin: 10px 0;
    font-size: 16px;
    color: #fff;
    background-color: #8A2BE2;
    border: none;
    border-radius: 5px;
    text-align: center;
    text-decoration: none;
    transition: background-color 0.3s ease, transform 0.2s ease;
  }
  
  .btn2:hover {
    background-color: #6c1db5;
    transform: scale(1.05);
  }
  
  .btn2:active {
    background-color: #58169a;
    transform: scale(0.98);
  }
  
  /* Form Container Style */
  .section {
    display: flex;
    flex-direction: column;
    padding: 7rem 2.5rem;
    justify-content: center;
    background-color: #f3f4f6;
    color: #4b5563;
  }
  
  .container {
    width: 100%;
    max-width: 28rem;
    margin: 0 auto;
    background-color: #ffffff;
    padding: 2rem;
    border-radius: 0.5rem;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
  }
  
  .title {
    font-size: 1.875rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 2rem;
  }
  
  form label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: 500;
  }
  
  form input[type="text"],
  form input[type="email"],
  form textarea {
    width: 100%;
    padding: 0.5rem;
    margin-bottom: 1rem;
    border: 1px solid #d1d5db;
    border-radius: 0.375rem;
  }
  
  form input[type="submit"] {
    display: block;
    width: 100%;
    padding: 0.75rem;
    background-color: #6c1db5;
    color: #ffffff;
    border: none;
    border-radius: 0.375rem;
    cursor: pointer;
  }
  
  form input[type="submit"]:hover {
    background-color: #8745c5;
  }